.productItem {
  width: 3.3rem;
  height: 3.6rem;
  margin-top: 0.3rem;
  background-color: #fff;
  border-radius: 3px;
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.1);
}
.productItem:nth-child(1),
.productItem:nth-child(2) {
  margin-top: 0;
}
.productItem:nth-child(2n + 1) {
  margin-right: 0.3rem;
}
.productItem .productImg {
  position: relative;
  width: 100%;
  height: 2rem;
  overflow: hidden;
  text-align: center;
  border-bottom: 1px solid #efefef;
}
.productItem .productImg img {
  max-height: 100%;
  max-width: 100%;
  object-fit: cover;
  font-size: 0.28rem;
  color: #999;
}
.productItem .productImg .new {
  display: block;
  width: 50px;
  height: 26px;
  text-align: center;
  line-height: 36px;
  position: absolute;
  top: -7px;
  left: -20px;
  background-color: #0058ff;
  color: #fff;
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
  font-size: 12px;
}
.productItem .productInfo {
  height: calc(100% - 2rem);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 0.15rem;
}
.productItem .productInfo .name {
  font-size: 0.3rem;
}
.productItem .productInfo .priceAndSale {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.productItem .productInfo .priceAndSale .price {
  color: red;
  font-size: 0.32rem;
  font-weight: 600;
}
.productItem .productInfo .priceAndSale .sale {
  color: #444;
  font-size: 0.28rem;
}
