.user-page {
  height: 100%;
  font-size: 0.28rem;
}
.user-page .user {
  height: calc(100% - 50px);
  overflow-y: auto;
}
.user-page .user .user-header {
  display: flex;
  align-items: center;
  height: 4rem;
  padding-left: 0.5rem;
  background-image: -webkit-gradient(linear, left top, left bottom, from(#99d9ea), to(#2d231f));
  background-image: linear-gradient(#99d9ea, #2d231f);
}
.user-page .user .user-header .user-avatar {
  width: 2.2rem;
  height: 2.2rem;
  border-radius: 100%;
  overflow: hidden;
}
.user-page .user .user-header .user-avatar img {
  width: 100%;
  height: 100%;
}
.user-page .user .user-header .user-info {
  padding-left: 0.5rem;
}
.user-page .user .user-header .user-info .p1 {
  line-height: 0.6rem;
}
.user-page .user .user-header .user-info .p1 .nickname {
  font-size: 0.48rem;
  color: #fff;
  margin-right: 0.2rem;
}
.user-page .user .user-header .user-info .p1 .gender {
  font-size: 0.24rem;
  color: #fff;
}
.user-page .user .user-header .user-info .p2 {
  line-height: 0.5rem;
  font-size: 0.28rem;
}
.user-page .user .user-header .user-info .p2 .label {
  color: #fff;
}
.user-page .user .user-header .user-info .p2 .value {
  color: #ff0;
}
.user-page .user .user-header .user-info .p3 {
  line-height: 0.5rem;
}
.user-page .user .user-header .user-info .p3 .signIn {
  padding: 0 10px;
  margin-top: 0.1rem;
  font-size: 0.28rem;
  font-weight: 600;
  color: #fff;
  letter-spacing: 1px;
  line-height: 0.52rem;
  border-radius: 3px;
  background-image: linear-gradient(30deg, #e8bd21, #442b2b);
}
.user-page .user .user-navs .list .nav-item {
  display: flex;
  align-items: center;
  padding: 0.35rem 0.3rem;
  border-bottom: 1px solid #ccc;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.user-page .user .user-navs .list .nav-item:active,
.user-page .user .user-navs .list .nav-item:focus {
  background: #ddd;
}
.user-page .user .user-navs .list .nav-item:first-child {
  border-top: 1px solid #ccc;
}
.user-page .user .user-navs .list .nav-item .item-left {
  width: 0.42rem;
  height: 0.42rem;
  margin-right: 0.2rem;
  color: #333;
}
.user-page .user .user-navs .list .nav-item .item-label {
  flex: 1 1;
  margin-right: 0.2rem;
}
.user-page .user .user-navs .list .nav-item .item-right.item-right__icon {
  width: 0.3rem;
  height: 0.3rem;
}
.user-page .user .user-navs .list .nav-item .item-right.item-right__span {
  text-align: right;
  color: #666;
}
