.link-up-game-page .page-container {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.link-up-game-page .page-container .skus-wrap {
  display: flex;
  padding: 0.3rem 0.2rem 0.5rem;
}
.link-up-game-page .page-container .skus-wrap .label {
  width: 1rem;
  height: 0.4rem;
  line-height: 0.4rem;
  margin-top: 0.2rem;
  margin-right: 0.1rem;
  font-size: 0.3rem;
  font-weight: 600;
}
.link-up-game-page .page-container .skus-wrap .skus {
  display: flex;
  flex-wrap: wrap;
}
.link-up-game-page .page-container .skus-wrap .skus .sku button {
  width: 1rem;
  height: 0.4rem;
  line-height: 0.4rem;
  text-align: center;
  margin: 0.2rem 0.1rem 0;
  background-color: #ff7b05;
  color: #fff;
  font-weight: 600;
  border-radius: 10rem;
}
.link-up-game-page .page-container .skus-wrap .skus .sku button.active {
  background-color: red;
}
.link-up-game-page .page-container .list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.link-up-game-page .page-container .list .item {
  width: 1.5rem;
  height: 1.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid #ddd;
  background-color: #fff;
  box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.1);
  margin: 0 0.4rem 0.5rem;
}
.link-up-game-page .page-container .list .item:active,
.link-up-game-page .page-container .list .item:focus {
  background-color: #fffdc5;
}
.link-up-game-page .page-container .list .item span {
  font-size: 0.6rem;
}
.link-up-game-page .page-container .list .item.hidden {
  background-color: #e99810;
}
.link-up-game-page .page-container .list .item.hidden span {
  color: #fff;
}
.link-up-game-page .page-container .list .item.match {
  background-color: transparent;
}
