.cart-page .page-container {
  overflow-y: auto;
  background-color: #f5f5f5;
}
.cart-page .page-container .total {
  color: #fff;
  line-height: 0.5rem;
  font-size: 0.28rem;
  padding: 0 0.2rem;
  background-color: #3b3e66;
}
.cart-page .page-container .list-wrap {
  position: relative;
}
.cart-page .page-container .list-wrap .placeholder {
  position: absolute;
  top: 0;
  width: 100%;
  height: 1.5rem;
  background-color: #3b3e66;
}
.cart-page .page-container .list-wrap .list {
  position: relative;
  z-index: 1;
  padding: 0.2rem;
}
.cart-page .page-container .list-wrap .list .item {
  margin-bottom: 0.2rem;
  padding: 0.2rem;
  border-radius: 5px;
  box-shadow: 0 0 6px 0 rgba(0, 0, 0, 0.1);
  background-color: #fff;
}
.cart-page .page-container .list-wrap .list .item .shop-row {
  display: flex;
  align-items: center;
}
.cart-page .page-container .list-wrap .list .item .shop-row .shop-name {
  flex: 1 1;
}
.cart-page .page-container .list-wrap .list .item .goods-in-shop .good {
  display: flex;
}
.cart-page .page-container .list-wrap .list .item .goods-in-shop .good .wrap {
  flex: 1 1;
}
.cart-page .page-container .list-wrap .list .item .goods-in-shop .good .wrap .inner {
  width: 100%;
  height: 2rem;
  display: flex;
}
.cart-page .page-container .list-wrap .list .item .goods-in-shop .good .wrap .inner .img {
  width: 2rem;
  min-width: 2rem;
  height: 2rem;
  overflow: hidden;
  margin-right: 0.2rem;
  background: #ffa3a9;
  text-align: center;
  border-bottom: 1px solid #efefef;
}
.cart-page .page-container .list-wrap .list .item .goods-in-shop .good .wrap .inner .img img {
  max-height: 100%;
  max-width: 100%;
  object-fit: cover;
  font-size: 0.28rem;
  color: #999;
}
.cart-page .page-container .list-wrap .list .item .goods-in-shop .good .wrap .inner .info {
  height: 100%;
  display: flex;
  flex-direction: column;
  padding: 0.15rem;
  flex: 1 1;
}
.cart-page .page-container .list-wrap .list .item .goods-in-shop .good .wrap .inner .info .good-name {
  font-size: 0.3rem;
  line-height: 0.44rem;
}
.cart-page .page-container .list-wrap .list .item .goods-in-shop .good .wrap .inner .info .good-sku {
  margin-top: 0.1rem;
  font-size: 0.24rem;
  line-height: 0.36rem;
  color: #999;
  background-color: #f7f7f7;
  padding: 0.05rem 0.05rem;
  border-radius: 2px;
}
.cart-page .page-container .list-wrap .list .item .goods-in-shop .good .wrap .operate {
  height: 0.8rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-left: 2.2rem;
}
.cart-page .page-container .list-wrap .list .item .goods-in-shop .good .wrap .operate .price {
  font-size: 0.36rem;
  font-weight: 600;
  color: #f00;
}
.cart-page .page-container .list-wrap .checkbox {
  margin-right: 0.2rem;
}
.cart-page .footer {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 1rem;
  line-height: 1rem;
  background-color: #ffffff;
  padding: 0 0.2rem;
  border-top: 1px solid #eeeeee;
  color: #666666;
}
.cart-page .footer .checkbox {
  height: 100%;
  min-height: 100%;
  line-height: normal;
  line-height: initial;
}
.cart-page .footer .checkbox .am-list-thumb:first-child {
  margin-right: 5px;
}
.cart-page .footer .right {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 100%;
}
.cart-page .footer .right .total-price {
  font-size: 0.28rem;
}
.cart-page .footer .right .total-price .price {
  color: #f08e65;
}
.cart-page .footer .right .total-price .price span:nth-child(2) {
  font-size: 0.36rem;
}
.cart-page .footer .right .button {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0.16rem 0.6rem;
  border-radius: 10rem;
  margin-left: 0.2rem;
  background-image: -webkit-gradient(linear, left top, right top, from(#f09636), to(#e65a28));
  background-image: linear-gradient(90deg, #f09636, #e65a28);
  color: #ffffff;
}
