.product-detail-page {
  height: 100%;
  background-color: #f5f5f5;
  font-size: 0.28rem;
  color: #333;
}
.product-detail-page .pd-page-container {
  height: calc(100% - 50px);
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
}
.product-detail-page .section {
  padding: 0 0.2rem;
  margin-bottom: 0.2rem;
  background-color: #fff;
}
.product-detail-page .section .section-title {
  padding: 0.3rem 0.2rem 0.3rem 0;
  border-bottom: 1px solid #ddd;
  color: #666;
}
.product-detail-page .pd-header .banner-wrap {
  position: relative;
  height: 6rem;
  overflow: hidden;
}
.product-detail-page .pd-header .banner-wrap .banner {
  height: 100%;
}
.product-detail-page .pd-header .banner-wrap .banner img {
  height: 6rem;
  max-width: 100%;
  object-fit: cover;
  vertical-align: top;
  touch-action: none;
  font-size: 0.28rem;
  color: #999;
}
.product-detail-page .pd-header .banner-wrap .new {
  display: block;
  width: 50px;
  height: 26px;
  text-align: center;
  line-height: 36px;
  position: absolute;
  top: -7px;
  left: -20px;
  z-index: 1;
  background-color: #0058ff;
  color: #fff;
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
  font-size: 12px;
}
.product-detail-page .pd-header .product-info {
  display: flex;
  padding: 0.15rem 0.2rem;
}
.product-detail-page .pd-header .product-info .product-info__left {
  flex: 1 1;
  display: flex;
  flex-direction: column;
  padding-right: 0.2rem;
}
.product-detail-page .pd-header .product-info .product-info__left .name {
  line-height: 0.45rem;
  font-size: 0.32rem;
  text-align: justify-all;
}
.product-detail-page .pd-header .product-info .product-info__left .price {
  display: flex;
  align-items: baseline;
  margin-top: 0.15rem;
}
.product-detail-page .pd-header .product-info .product-info__left .price .price-now {
  color: #f00;
}
.product-detail-page .pd-header .product-info .product-info__left .price .price-now span:first-child {
  font-size: 0.3rem;
}
.product-detail-page .pd-header .product-info .product-info__left .price .price-now span:nth-child(2) {
  font-size: 0.48rem;
}
.product-detail-page .pd-header .product-info .product-info__left .price .price-origin {
  margin-left: 0.2rem;
  color: #999;
  text-decoration: line-through;
}
.product-detail-page .pd-header .product-info .product-info__left .price .price-origin span:first-child {
  font-size: 0.28rem;
}
.product-detail-page .pd-header .product-info .product-info__left .price .price-origin span:nth-child(2) {
  font-size: 0.32rem;
}
.product-detail-page .pd-header .product-info .product-info__right {
  width: 1rem;
  display: flex;
  align-items: center;
  justify-content: center;
}
.product-detail-page .pd-header .product-info .product-info__right .share {
  text-align: center;
  color: #666;
}
.product-detail-page .pd-header .product-info .product-info__right .share .icon-svg {
  display: block;
  width: 0.4rem;
  height: 0.4rem;
  margin: 0 auto 0.15rem;
  color: #555;
}
.product-detail-page .pd-header .product-info .product-info__right .share span {
  font-size: 0.28rem;
}
.product-detail-page .pd-body .ul1 .item {
  display: flex;
  align-items: center;
  padding: 0.3rem 0;
  border-bottom: 1px solid #ddd;
}
.product-detail-page .pd-body .ul1 .item:last-child {
  border-bottom: none;
}
.product-detail-page .pd-body .ul1 .item .key {
  color: #666;
}
.product-detail-page .pd-body .ul1 .item .value {
  color: #333;
}
.product-detail-page .pd-body .coupon {
  width: 100%;
  overflow-x: auto;
}
.product-detail-page .pd-body .coupon .coupon-list {
  display: flex;
  flex-wrap: wrap;
  padding: 0.3rem 0 0.1rem;
}
.product-detail-page .pd-body .coupon .coupon-list .item {
  min-width: 2.233333rem;
  margin-right: 0.2rem;
  margin-bottom: 0.2rem;
  padding: 0.1rem 0.05rem 0.13rem;
  border-radius: 5px;
  color: rgba(250, 250, 250, 0.9);
  text-align: center;
  background-color: #ff6208;
  background-image: linear-gradient(45deg, #ff6208, #ea333f);
  box-shadow: 0 0 5px 0 rgba(255, 98, 8, 0.4);
}
.product-detail-page .pd-body .coupon .coupon-list .item:nth-child(3n+3) {
  margin-right: 0;
}
.product-detail-page .pd-body .coupon .coupon-list .item .price {
  margin-bottom: 0.05rem;
}
.product-detail-page .pd-body .coupon .coupon-list .item .price span {
  vertical-align: middle;
}
.product-detail-page .pd-body .coupon .coupon-list .item .price span:first-child {
  font-size: 0.3rem;
}
.product-detail-page .pd-body .coupon .coupon-list .item .price span:nth-child(2) {
  font-size: 0.36rem;
}
.product-detail-page .pd-body .coupon .coupon-list .item .expire {
  font-size: 0.28rem;
}
.product-detail-page .pd-body .ul4 {
  display: flex;
  align-items: center;
  padding: 0.35rem 0;
}
.product-detail-page .pd-body .ul4 .one {
  flex: 1 1;
  text-align: center;
}
.product-detail-page .pd-body .ul4 .one .icon-svg {
  width: 0.4rem;
  height: 0.4rem;
  vertical-align: text-top;
  color: #002aff;
}
.product-detail-page .pd-body .ul4 .one span {
  margin-left: 0.05rem;
}
.product-detail-page .pd-body .sku {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0.35rem 0;
}
.product-detail-page .pd-body .sku .label {
  color: #666;
}
.product-detail-page .pd-body .sku .value span {
  margin-right: 0.05rem;
}
.product-detail-page .pd-footer {
  height: 50px;
}
.product-detail-page .pd-footer .footer {
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 50px;
  display: flex;
  align-items: center;
  background-color: #fff;
}
.product-detail-page .pd-footer .footer .icon {
  flex: 2 1;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  color: #888;
}
.product-detail-page .pd-footer .footer .icon .icon-svg {
  width: 0.4rem;
  height: 0.4rem;
  margin-bottom: 0.08rem;
  color: #555;
}
.product-detail-page .pd-footer .footer .text {
  flex: 5 1;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  color: #fff;
  font-size: 0.32rem;
}
.product-detail-page .pd-footer .footer .text.text-cart {
  background-color: #f19a37;
}
.product-detail-page .pd-footer .footer .text.text-buy {
  background-color: #ea333f;
}
