body {
  font-size: 0.28rem;
}
#root {
  height: 100%;
}
.AppTabBar .am-tab-bar {
  position: fixed;
  width: 100%;
  height: 50px;
  bottom: 0;
}
.text-overflow,
.text-overflow-2,
.text-overflow-3 {
  max-width: 100%;
  overflow: hidden;
  text-overflow: ellipsis;
}
.text-overflow {
  white-space: nowrap;
}
.text-overflow-2,
.text-overflow-3 {
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
}
.text-overflow-3 {
  -webkit-line-clamp: 3;
}
.page {
  height: 100%;
}
.page .page-container {
  height: 100%;
  overflow-y: auto;
  background-color: #f5f5f5;
}
.page .page-container.loading {
  display: flex;
  justify-content: center;
  padding-top: 30%;
  color: #999;
  font-size: 0.36rem;
}
.page .checkbox {
  width: auto;
  padding: 0;
  height: 0.6rem;
}
.page .checkbox.am-list-item-active {
  background: transparent;
}
.page .checkbox .am-list-thumb,
.page .checkbox .am-list-thumb:first-child,
.page .checkbox .am-list-line {
  margin: 0;
  padding: 0;
}
.page .checkbox .am-list-line .am-list-content {
  font-size: 0.28rem;
  color: #666;
}
.page .checkbox .am-checkbox .am-checkbox-inner {
  width: 100%;
  height: 100%;
}
.page .checkbox .am-checkbox .am-checkbox-inner:after {
  top: 3.5px;
  right: 8px;
  width: 4px;
  height: 9px;
  border-style: solid;
  border-width: 0 1.5px 1.5px 0;
}
.page .checkbox .am-checkbox.am-checkbox-checked .am-checkbox-inner {
  background: #f00;
  border-color: #f00;
  border: none;
  background-image: linear-gradient(60deg, #ffc800, #ea333f);
}
