.homePage .bannerBox .imgItem {
  width: 100%;
  height: 4rem;
  line-height: 4rem;
}
.homePage .bannerBox .imgItem img {
  width: 100%;
  max-height: 100%;
  object-fit: cover;
}
.homePage .cardBox .cardItem {
  display: flex;
  align-items: center;
  width: calc(50% - 0.3rem);
  height: 1.8rem;
  padding: 0.2rem;
  margin: 0 0.2rem 0.2rem 0;
  background: #fff;
  border-radius: 0.02rem;
  overflow: hidden;
}
.homePage .cardBox .cardItem:nth-child(1) {
  margin-top: 0.2rem;
  margin-left: 0.2rem;
}
.homePage .cardBox .cardItem:nth-child(2) {
  margin-top: 0.2rem;
}
.homePage .cardBox .cardItem:nth-child(3) {
  margin-left: 0.2rem;
}
.homePage .cardBox .cardItem .cardItem__img {
  width: 1.6rem;
  height: 1.6rem;
  border: 1px solid #ccc;
}
.homePage .cardBox .cardItem .cardItem__right {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  flex: 1 1;
  height: 100%;
  padding: 10px;
}
.homePage .cardBox .cardItem .cardItem__right .title {
  color: #333;
  font-weight: 600;
}
.homePage .cardBox .cardItem .cardItem__right .number {
  font-size: 12px;
  color: #999;
}
