.app-header {
  display: flex;
  align-items: center;
  height: 0.8rem;
  line-height: 0.8rem;
  font-size: 0.32rem;
  font-weight: 600;
  background-color: #fff;
}
.app-header .app-header-left {
  font-size: 0.42rem;
  margin-left: 0.2rem;
}
.app-header .app-header-title {
  flex: 1 1;
  text-align: center;
}
.app-header .app-header-right {
  width: 1rem;
  padding-right: 0.2rem;
  text-align: right;
}
